@import url(https://fonts.googleapis.com/css2?family=Iceberg&display=swap);
body{margin:0;background-color:#c89933}.App{min-height:100vh;display:flex;justify-content:center;align-items:center}

.Carousel{padding-top:10px;background-color:#f1f7ed;box-shadow:8px 12px 12px rgba(0,0,0,0.25);border-radius:4px;overflow-x:hidden}.Carousel__list{margin:0 0 10px;padding:0;display:flex;flex-direction:row;list-style:none}.Carousel__buttons{display:flex;flex-direction:row;justify-content:space-between}.Carousel__button{padding:20px 16px;width:100%;font-family:"Iceberg", sans-serif;font-size:24px;line-height:100%;text-transform:uppercase;color:#f1f7ed;border:none;background-image:linear-gradient(360deg, #54494b 0%, rgba(84,73,75,0.8) 100%)}.Carousel__button:first-child{border-radius:0 0 0 4px}.Carousel__button:last-child{border-radius:0 0 4px}.Carousel__button:disabled{color:#54494b;background-color:#b0a5a7;background-image:none}.Carousel__button:not(:disabled):hover{background-color:#54494b;background-image:none}

